"use client"

import type React from "react"

import { useState, useRef } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Upload, Download, FileText, Languages } from "lucide-react"
import { saveAs } from "file-saver"
import { saveGeneratedFile, generateFileId, formatFileSize } from "@/lib/file-storage"
import { useToast } from "@/hooks/use-toast"

// Ensure a safe browser env shape before any dynamic imports used by translation
;(function ensureBrowserEnv() {
  const g: any = globalThis as any
  if (!g.process || typeof g.process !== 'object') {
    g.process = { versions: {}, env: {}, browser: true }
  } else {
    const p = g.process as any
    if (p.versions == null) p.versions = {}
    if (p.env == null) p.env = {}
    p.browser = true
  }
})()

export function PDFTranslateScreen() {
  const router = useRouter()
  const { toast } = useToast()
  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [isTranslating, setIsTranslating] = useState(false)
  const [sourceLang, setSourceLang] = useState<"es" | "en" | "fr" | "de" | "it" | "pt">("es")
  const [targetLang, setTargetLang] = useState<"es" | "en" | "fr" | "de" | "it" | "pt">("en")
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file && file.type === "application/pdf") {
      setPdfFile(file)
    } else {
      toast({ title: "Archivo inválido", description: "Selecciona un PDF válido", variant: "destructive" })
    }
  }

  const translatePDF = async () => {
    if (!pdfFile) return
    if (sourceLang === targetLang) {
      toast({ title: "Idiomas iguales", description: "Selecciona idiomas diferentes", variant: "destructive" })
      return
    }

    setIsTranslating(true)
    try {
      // Dynamic import to avoid SSR issues
      const { TranslationService } = await import("@/lib/services/translation-service")
      const blob = await TranslationService.translatePDF({ file: pdfFile, sourceLang, targetLang })

      const baseName = pdfFile.name.replace(/\.pdf$/i, '')
      const fileName = `${baseName}.${sourceLang}-${targetLang}.translated.pdf`

      saveAs(blob, fileName)

      saveGeneratedFile({
        id: generateFileId(),
        name: fileName,
        type: "PDF Traducido",
        date: new Date().toLocaleDateString(),
        size: formatFileSize(blob.size),
      })

      toast({ title: "Traducción exitosa", description: `Se generó ${fileName}` })

      setTimeout(() => {
        router.push("/dashboard")
      }, 1000)
    } catch (error: any) {
      console.error("[v0] Error translating PDF:", error)
      toast({
        title: "Error",
        description: error?.message || "No se pudo traducir el PDF (verifica modelos locales)",
        variant: "destructive",
      })
    } finally {
      setIsTranslating(false)
    }
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardContent className="pt-6">
          <div className="space-y-4">
            <Label>Seleccionar PDF</Label>
            <Button
              variant="outline"
              className="w-full h-32 border-dashed bg-transparent"
              onClick={() => fileInputRef.current?.click()}
            >
              <div className="flex flex-col items-center gap-2">
                <Upload className="w-8 h-8 text-muted-foreground" />
                <span className="text-sm text-muted-foreground">Click para subir archivo PDF</span>
              </div>
            </Button>
            <input
              ref={fileInputRef}
              type="file"
              accept="application/pdf"
              className="hidden"
              onChange={handleFileUpload}
            />
          </div>
        </CardContent>
      </Card>

      {pdfFile && (
        <>
          <Card>
            <CardContent className="pt-6 space-y-6">
              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-secondary rounded-lg flex items-center justify-center">
                  <FileText className="w-6 h-6 text-primary" />
                </div>
                <div className="flex-1">
                  <p className="font-medium">{pdfFile.name}</p>
                  <p className="text-sm text-muted-foreground">{(pdfFile.size / 1024 / 1024).toFixed(2)} MB</p>
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label>Idioma origen</Label>
                  <Select value={sourceLang} onValueChange={(v) => setSourceLang(v as any)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecciona idioma de origen" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="es">Español</SelectItem>
                      <SelectItem value="en">Inglés</SelectItem>
                      <SelectItem value="fr">Francés</SelectItem>
                      <SelectItem value="de">Alemán</SelectItem>
                      <SelectItem value="it">Italiano</SelectItem>
                      <SelectItem value="pt">Portugués</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <Label>Idioma destino</Label>
                  <Select value={targetLang} onValueChange={(v) => setTargetLang(v as any)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecciona idioma destino" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="en">Inglés</SelectItem>
                      <SelectItem value="es">Español</SelectItem>
                      <SelectItem value="fr">Francés</SelectItem>
                      <SelectItem value="de">Alemán</SelectItem>
                      <SelectItem value="it">Italiano</SelectItem>
                      <SelectItem value="pt">Portugués</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="p-6 bg-gradient-to-br from-primary/10 to-accent/10 rounded-lg border-2 border-dashed border-primary/20">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 bg-primary/20 rounded-full flex items-center justify-center">
                    <Languages className="w-6 h-6 text-primary" />
                  </div>
                  <div className="flex-1">
                    <p className="text-sm text-muted-foreground">
                      Traduce el contenido del PDF a {targetLang.toUpperCase()} usando modelos locales (sin API).
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Button onClick={translatePDF} className="w-full" size="lg" disabled={isTranslating}>
            <Download className="w-4 h-4 mr-2" />
            {isTranslating ? "Traduciendo..." : "Traducir PDF"}
          </Button>
        </>
      )}
    </div>
  )
}